
.general {
    font-family: Arial, Arial, Helvetica, sans-serif
}
.header {
    background-color: white;
    border-bottom: 1px solid gainsboro;
}

.nav {
    padding-top: 10px;
}
p { 
    font-family: 'Arial', 'Helvetica Neue', Helvetica, sans-serif;
}

.prof-pic {
    width: 100%;
    border-radius: 50%;
    display: block;
    margin-left: auto;
    margin-right: auto;
    max-width: 250px;
    padding-top: auto;
    padding-bottom: auto;
}

.img-container {
    height: 100%;
}
/* 
.blurb {
    font-size: 13px;
} */

.title-row {
    height: 36px;
    margin-left: 0px;
    margin-right: 0px;
}

.body-row {
    margin-left: 0px;
    margin-right: 0px;
    padding-top: 10px;
    font-size: 13px;
}



@media only screen and (min-width: 1199px) {
    .title-row {
        height: auto;
        padding-top: 10px;
    }

    .prof-pic {
        max-width: 325px;
    }
}

@media only screen and (max-width: 991px) {
    .title-row {
        height: auto;
        padding-top: 10px;
    }

    .body-row {
        padding-top: 5px;
    }

    #skills {
        display: block;
        margin-left: auto;
        margin-right: auto;
        font-size: 14px;
    }
}



@media only screen and (max-width:425px) {

    #skills {
        font-size: 13px;
    }
}


.overlay {
    opacity: 0.9;
}

a {
    color: black;
}

@media only screen and (max-width: 991px) {
    .project-info {
        font-size: 13px;
    }
}

@media only screen and (max-width: 767px) and (min-width: 425px) {
    .project-info {
        font-size: 16px;
    }
}

@media only screen and (max-width: 374px) {
    .project-title {
        font-size: 26px;
    }
    .buffer {
        display: none;
    }
    .project-info {
        font-size: 11px;
        margin: 10px !important;
    }
}
html {
    overflow-x: hidden;
    min-height: 100vh;
    height: 100%;
    position: relative;
}

body { 
    overflow-x: hidden;
    background-image: -webkit-linear-gradient(top left, #141e30, #243b55);
    background-image: linear-gradient(to bottom right, #141e30, #243b55);
    height: 100%;
}
